export class LightGroupRepresentation {
    externalId?: string;
    groups?: LightGroupRepresentation[];
    id?: number;
    name?: string;
    status?: string;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
            if (input.groups) {
                this.groups = input.groups.map((item: any) => {
                    return new LightGroupRepresentation(item);
                });
            }
        }
    }

}
